"use client";

import React from 'react';

interface Renewal {
  id: string;
  customerName: string;
  customerEmail: string;
  contractId: string;
  expiryDate: string;
  daysRemaining: number;
  status: 'Pending' | 'Overdue';
}

const mockRenewals: Renewal[] = [
  {
    id: '1',
    customerName: 'TechSolutions Inc.',
    customerEmail: 'support@techsol.com',
    contractId: 'AMC-2023-015',
    expiryDate: 'Jun 15, 2023',
    daysRemaining: 5,
    status: 'Pending'
  },
  {
    id: '2',
    customerName: 'Greenfield Mfg.',
    customerEmail: 'accounts@greenfield.com',
    contractId: 'AMC-2023-042',
    expiryDate: 'Jun 18, 2023',
    daysRemaining: 8,
    status: 'Pending'
  },
  {
    id: '3',
    customerName: 'DigitalWave Tech',
    customerEmail: 'admin@digitalwave.com',
    contractId: 'AMC-2022-107',
    expiryDate: 'May 28, 2023',
    daysRemaining: -7,
    status: 'Overdue'
  },
  {
    id: '4',
    customerName: 'InnovateCorp Ltd.',
    customerEmail: 'contracts@innovate.com',
    contractId: 'AMC-2023-089',
    expiryDate: 'Jun 22, 2023',
    daysRemaining: 12,
    status: 'Pending'
  },
  {
    id: '5',
    customerName: 'Global Systems',
    customerEmail: 'renewals@globalsys.com',
    contractId: 'AMC-2023-003',
    expiryDate: 'Jun 10, 2023',
    daysRemaining: -2,
    status: 'Overdue'
  }
];

export default function RenewalsTable() {
  const getInitials = (name: string): string => {
    return name
      .split(' ')
      .map(word => word.charAt(0))
      .join('')
      .substring(0, 2)
      .toUpperCase();
  };

  const getStatusBadge = (status: string) => {
    const baseClasses = "px-2 py-1 text-xs font-medium rounded-full";
    if (status === 'Overdue') {
      return `${baseClasses} bg-red-100 text-red-800`;
    }
    return `${baseClasses} bg-yellow-100 text-yellow-800`;
  };

  const getDaysText = (daysRemaining: number) => {
    if (daysRemaining < 0) {
      return `Overdue by ${Math.abs(daysRemaining)} days`;
    }
    return `${daysRemaining} days left`;
  };

  if (!mockRenewals || mockRenewals.length === 0) {
    return (
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-8 text-center">
        <p className="text-gray-500">No upcoming renewals available</p>
      </div>
    );
  }

  return (
    <div className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden">
      {/* Header */}
      <div className="px-6 py-4 border-b border-gray-200">
        <div className="flex items-center justify-between">
          <h2 className="text-lg font-semibold text-gray-800">Upcoming AMC Renewals</h2>
          <div className="flex space-x-2">
            <button className="px-3 py-1 text-sm bg-indigo-600 text-white rounded hover:bg-indigo-700 transition-colors">
              ↻ Refresh
            </button>
            <button className="px-3 py-1 text-sm border border-gray-300 rounded hover:bg-gray-50 transition-colors">
              ⚙ Filter
            </button>
          </div>
        </div>
      </div>

      {/* Table */}
      <div className="overflow-x-auto">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Customer
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Contract ID
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Expiry Date
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Status
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Actions
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {mockRenewals.map((renewal) => (
              <tr 
                key={renewal.id} 
                className={`hover:bg-gray-50 transition-colors ${
                  renewal.status === 'Overdue' ? 'bg-red-50' : ''
                }`}
              >
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="flex items-center">
                    <div className="flex-shrink-0 h-10 w-10">
                      <div className="h-10 w-10 rounded-full bg-indigo-600 text-white flex items-center justify-center text-sm font-bold">
                        {getInitials(renewal.customerName)}
                      </div>
                    </div>
                    <div className="ml-4">
                      <div className="text-sm font-medium text-gray-900">
                        {renewal.customerName}
                      </div>
                      <div className="text-sm text-gray-500">
                        {renewal.customerEmail}
                      </div>
                    </div>
                  </div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                  {renewal.contractId}
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm text-gray-900">{renewal.expiryDate}</div>
                  <div className={`text-sm ${renewal.daysRemaining < 0 ? 'text-red-600' : 'text-gray-500'}`}>
                    {getDaysText(renewal.daysRemaining)}
                  </div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <span className={getStatusBadge(renewal.status)}>
                    {renewal.status}
                  </span>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                  <button className="text-indigo-600 hover:text-indigo-900 mr-3 hover:underline">
                    Renew
                  </button>
                  <button className="text-gray-600 hover:text-gray-900 hover:underline">
                    Notify
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}
